body {
  background: #00e9ff75;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.formContainer {
  padding: 2rem;
  max-width: 600px;
  background-image: url(./images/formback.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  margin: 2.9rem auto;
  border-radius: 20px;
  /* box-shadow: 0px 0px 16px 4px #ececec; */
  width: 50%;
  text-align: center;
}
#demo-controlled-radio-buttons-group {
  font-size: 20px;
  font-weight: bold;
  font-family: montserrat;
}
.MuiOutlinedInput-root {
  background: white;
}
.fullform {
  margin: 1rem auto;
  width: 100%;
}
.MuiTextField-root {
  width: 100%;
}
.form-skills {
  color: #666565;
  font-family: montserrat;
  font-size: 20px;
  font-weight: bold;
  margin-top: 1rem;
}
#outline-basic {
  letter-spacing: 2px;
}
.MuiFormControlLabel-root {
  width: 20%;
}
#demo-radio-buttons-group-label {
  color: #565656;
  font-weight: bold;
  font-size: 20px;
  font-family: emoji;
}
.required-field {
  color: red;
  font-family: montserrat;
  font-size: 15px;
  font-weight: 500;
}
.MuiButton-root {
  font-weight: bold;
}
.MuiInputLabel-root {
  color: white;
}
